<script>

export default {
    name: 'Home',
    data(){
        return {
            bunge: true,
            items: data,
            sets: [],
            countOfPage: 5,
            currPage: 1,
            filter_name: ''  
        }
    },

    created(){
        this.main()
    },

    filters: {
        addCero: function (value) {
            if (value < 10) return '0'+value
            if(value > 50) return 50 
            return value
        }
    },

    methods: {
        changeType(value){
            this.bunge = value
        },

        union(r){
            let a = {
                head:'',
                body: '',
                glass: '',
                flag: '',
                item1: 0,
                item2: 0,
                item3: 0,
                item4: 0,
                item5: 0,
                item6: 0,
                item7: 0,
                item8: 0,
                cash: 0,
                gold: 0
            }

            for(let i = 0; i < r.length; i++){
                // item.name.push({name : r[i].name})
                // Creamos nuevo objeto

                // console.log(i)
                if(i==0){
                    a.head=r[i].name
                    // a.name.push({name : r[i].name, type: 'head'})
                }else if(i==1){
                    a.body=r[i].name
                    // a.name.push({name : r[i].name, type: 'body'})
                }else if(i==2){
                    a.glass=r[i].name
                    // a.name.push({name : r[i].name, type: 'glass'})
                }else if(i==3){
                    a.flag=r[i].name
                    // a.name.push({name : r[i].name, type: 'flag'})
                }

                for(let j = 1; j <= 8; j++){
                    a[`item${ j }`] += r[i][`item${ j }`]
                }

                a.cash+=r[i].cash
                a.gold+=r[i].gold
            }
            return a
        },

        main(){

            //Número total de combinaciones
            let total = this.items.reduce(function (t, c) {  return t * c.length }, 1)

            //Se realizan todas las combinaciones
            let combinations = cartesian(this.items[0], this.items[1], this.items[2], this.items[3])

            let sets = []
            //Concatena la suma de combinaciones
            for(let index = 0; index < total; index++){
                sets.push(this.union(combinations.next().value))
            }

            console.log("sets", sets)

            // Ordenamos
            this.sets = multiSort(sets, {
                item2: 'desc',
                item1: 'desc', 
                item3: 'desc',
                item4: 'desc',
                item5: 'desc',
                item6: 'desc',
                item7: 'desc',
                item8: 'desc',
            })
        },
        setPage: function(idx){
            if( idx <= 0 || idx > this.totalPage ){
                return;
            }
            this.currPage = idx;
        },

    },

    computed: {
        filteredSets: function(){

            var filter_name = this.filter_name.toLowerCase();

            return ( this.filter_name.trim() !== '' ) ? 
                this.sets.filter(function(d){
                     return d.head.toLowerCase().indexOf(filter_name) > -1 || d.body.toLowerCase().indexOf(filter_name) > -1 || d.glass.toLowerCase().indexOf(filter_name) > -1 || d.flag.toLowerCase().indexOf(filter_name) > -1; 
                }) : 
            this.sets;
        },

        pageStart: function(){
            return (this.currPage - 1) * this.countOfPage;
        },

        totalPage: function(){
            return Math.ceil(this.filteredSets.length / this.countOfPage);
        }
    },

}
</script>

